import { Implementation } from './Implementation';
// import { Application } from '../';
export class AppTypescript {
    implementation: Implementation;
    host: HTMLElement;
    divText: HTMLElement;
    public static Init(id: string) {
        new AppTypescript(id);
    }
    constructor(id: string = 'main') {
        // console.log('id: ' + id + ', App Init!!!!!');
        const host = document.getElementById(id);
        if (host) {
            this.host = host;
        }
        const divText = document.createElement('div');
        if (divText) {
            this.divText = divText;
            this.host.append(this.divText);
        }
        this.implementation = new Implementation(this);
    }
}

export class Solution {
    public divHost: HTMLElement;

    constructor(id: string) {
        this.divHost = document.getElementById(id);

        AppTypescript.Init(id)
    }
    public destroy() {
        if (this.divHost) {
            try {
                while (this.divHost.firstChild) {
                    this.divHost.removeChild(this.divHost.firstChild);
                }
            } catch (error) {
                console.error('Error in destroy method:', error);
            }
        } else {
            console.warn('divHost is undefined in destroy method');
        }
    }
}